const NIM = require('./NIM_Web_SDK_v8.4.0.js').NIM

const initNIM = function(){
	const userInfo = getApp().globalData.userInfo
	let IMData = {friends:[]}
	getApp().globalData.IMData = IMData
	
	function onConnect() {
	    console.log('');
	}
	function onWillReconnect(obj) {
	    //  `SDK` , , 
	    console.log('', obj);
	}
	function onDisconnect(error) {
	    //  `SDK` , , 
	    console.log('', error);
	    if (error) {
	        switch (error.code) {
	        // , 
	        case 302:
	            break;
	        // , , 
	        case 417:
	            break;
	        // , 
	        case 'kicked':
	            break;
	        default:
	            break;
	        }
	    }
	}
	function onError(error, obj) {
	    console.log('', error, obj);
	}
	
	function onLoginPortsChange(loginPorts) {
	    console.log('', loginPorts);
	}
	
	function onBlacklist(blacklist) {
	    console.log('', blacklist);
	    data.blacklist = nim.mergeRelations(data.blacklist, blacklist);
	    data.blacklist = nim.cutRelations(data.blacklist, blacklist.invalid);
	    refreshBlacklistUI();
	}
	function onMarkInBlacklist(obj) {
	    console.log(obj.account + '' + (obj.isAdd ? '' : '') + '', obj);
	    if (obj.isAdd) {
	        addToBlacklist(obj);
	    } else {
	        removeFromBlacklist(obj);
	    }
	}
	function addToBlacklist(obj) {
	    data.blacklist = nim.mergeRelations(data.blacklist, obj.record);
	    refreshBlacklistUI();
	}
	function removeFromBlacklist(obj) {
	    data.blacklist = nim.cutRelations(data.blacklist, obj.record);
	    refreshBlacklistUI();
	}
	function refreshBlacklistUI() {
	    // 
	}
	function onMutelist(mutelist) {
	    console.log('', mutelist);
	    data.mutelist = nim.mergeRelations(data.mutelist, mutelist);
	    data.mutelist = nim.cutRelations(data.mutelist, mutelist.invalid);
	    refreshMutelistUI();
	}
	function onMarkInMutelist(obj) {
	    console.log(obj.account + '' + (obj.isAdd ? '' : '') + '', obj);
	    if (obj.isAdd) {
	        addToMutelist(obj);
	    } else {
	        removeFromMutelist(obj);
	    }
	}
	function addToMutelist(obj) {
	    data.mutelist = nim.mergeRelations(data.mutelist, obj.record);
	    refreshMutelistUI();
	}
	function removeFromMutelist(obj) {
	    data.mutelist = nim.cutRelations(data.mutelist, obj.record);
	    refreshMutelistUI();
	}
	function refreshMutelistUI() {
	    // 
	}
	
	function onFriends(friends) {
		const nim = getApp().globalData.nim
	    console.log('', friends);
	    IMData.friends = nim.mergeFriends(IMData.friends, friends);
	    IMData.friends = nim.cutFriends(IMData.friends, friends.invalid);
	}
	function onSyncFriendAction(obj) {
	    console.log('', obj);
	    switch (obj.type) {
	    case 'addFriend':
	        console.log('' + obj);
	        onAddFriend(obj.friend);
	        break;
	    case 'applyFriend':
	        console.log('' + obj);
	        break;
	    case 'passFriendApply':
	        console.log('' + obj);
	        onAddFriend(obj.friend);
	        break;
	    case 'rejectFriendApply':
	        console.log('' + obj);
	        break;
	    case 'deleteFriend':
	        console.log('' + obj);
	        onDeleteFriend(obj.account);
	        break;
	    case 'updateFriend':
	        console.log('', obj);
	        onUpdateFriend(obj.friend);
	        break;
	    }
	}
	function onAddFriend(friend) {
	    data.friends = nim.mergeFriends(data.friends, friend);
	    refreshFriendsUI();
	}
	function onDeleteFriend(account) {
	    data.friends = nim.cutFriendsByAccounts(data.friends, account);
	    refreshFriendsUI();
	}
	function onUpdateFriend(friend) {
	    data.friends = nim.mergeFriends(data.friends, friend);
	    refreshFriendsUI();
	}
	function refreshFriendsUI() {
	    // 
	}
	
	function onMyInfo(user) {
	    console.log('', user);
	    IMData.myInfo = user;
	}
	function onUpdateMyInfo(user) {
	    console.log('', user);
	    data.myInfo = NIM.util.merge(data.myInfo, user);
	    updateMyInfoUI();
	}
	function updateMyInfoUI() {
	    // 
	}
	function onUsers(users) {
	    console.log('', users);
	    IMData.users = nim.mergeUsers(IMData.users, users);
	}
	function onUpdateUser(user) {
	    console.log('', user);
	    IMData.users = nim.mergeUsers(IMData.users, user);
	}
	
	function onSuperTeams (superTeams) {
	  console.log('', superTeams)
	  IMData.superTeams = nim.mergeTeams(IMData.superTeams, superTeams)
	  onInvalidSuperTeams(superTeams.invalid)
	}
	function onInvalidSuperTeams (teams) {
	  IMData.superTeams = nim.cutTeams(IMData.superTeams, teams)
	  IMData.invalidSuperTeams = nim.mergeTeams(IMData.invalidSuperTeams, teams)
	  refreshSuperTeamsUI()
	}
	
	function onSyncCreateSuperTeam (team, owner) {
	  console.log(' onSyncCreateSuperTeam ', team, owner)
	  IMData.superTeams = nim.mergeTeams(IMData.superTeams, team)
	  refreshSuperTeamsUI()
	  onSuperTeamMembers({
	    teamId: team.teamId,
	    members: owner
	  })
	}
	function onAddSuperTeamMembers (team, accounts, members) {
	  console.log(' onAddSuperTeamMembers ', team, accounts, members)
	  if (!accounts && !members) {
	    accounts = team.accounts || []
	    members = team.members || []
	    team = team.team || {}
	  }
	  var teamId = team.teamId
	  
	   // 
	   // 
	  
	  if (accounts.indexOf(data.account) === -1) {
	    onSuperTeamMembers({
	      teamId: teamId,
	      members: members
	    })
	  } else {
	    // ...
	  }
	  onSuperTeams(team)
	}
	function onDismissSuperTeam(obj) {
	 console.log(' onDismissSuperTeam', obj);
	  var teamId = obj.teamId
	  removeAllSuperTeamMembers(teamId)
	  IMData.superTeams = nim.cutTeams(IMData.superTeams, obj)
	  refreshSuperTeamsUI()
	  refreshSuperTeamMembersUI()
	}
	function onRemoveSuperTeamMembers (obj) {
	  console.log(' onRemoveSuperTeamMembers ', obj.accounts, obj)
	  var teamId = obj.team.teamId
	  var accounts = obj.accounts
	  var team
	  if (!teamId && !accounts) {
	    accounts = obj.accounts || []
	  }
	  // 
	  // 
	  if (accounts.indexOf(IMData.account) === -1) {
	     if (team) {
	      onSuperTeams(team)
	    }
	    if (!IMData.superTeamMembers) {
	      IMData.superTeamMembers = {}
	    }
	    IMData.superTeamMembers[teamId] = nim.cutTeamMembersByAccounts(
	      IMData.superTeamMembers[teamId],
	      teamId,
	      accounts
	    )
	     refreshSuperTeamMembersUI()
	  } else {
	    leaveSuperTeam(teamId)
	  }
	}
	function onUpdateSuperTeam(err, msg) {
	  console.log(' teamId', err, msg)
	}
	function onUpdateSuperTeamMember (member) {
	  console.log('', member) 
	}
	function leaveSuperTeam (teamId) {
	  onInvalidSuperTeams({
	    teamId: teamId
	  })
	  removeAllSuperTeamMembers(teamId)
	}
	function refreshSuperTeamsUI () {
	}
	function refreshSuperTeamMembersUI () {
	}
	function removeAllSuperTeamMembers () {
	}
	function onTeams(teams) {
	    console.log('', teams);
	    IMData.teams = nim.mergeTeams(IMData.teams, teams);
	    onInvalidTeams(teams.invalid);
	}
	function onInvalidTeams(teams) {
	    IMData.teams = nim.cutTeams(IMData.teams, teams);
	    IMData.invalidTeams = nim.mergeTeams(IMData.invalidTeams, teams);
	    refreshTeamsUI();
	}
	function onCreateTeam(team) {
	    console.log('', team);
	    IMData.teams = nim.mergeTeams(IMData.teams, team);
	    refreshTeamsUI();
	    onTeamMembers({
	        teamId: team.teamId,
	        members: owner
	    });
	}
	function refreshTeamsUI() {
	    // 
	}
	function onTeamMembers(obj) {
	    console.log('', obj);
	    var teamId = obj.teamId;
	    var members = obj.members;
	    IMData.teamMembers = IMData.teamMembers || {};
	    IMData.teamMembers[teamId] = nim.mergeTeamMembers(IMData.teamMembers[teamId], members);
	    IMData.teamMembers[teamId] = nim.cutTeamMembers(IMData.teamMembers[teamId], members.invalid);
	    refreshTeamMembersUI();
	}
	function onSyncTeamMembersDone() {
	    console.log('');
	}
	function onUpdateTeamMember(teamMember) {
	    console.log('', teamMember);
	    onTeamMembers({
	        teamId: teamMember.teamId,
	        members: teamMember
	    });
	}
	function refreshTeamMembersUI() {
	    // 
	}
	
	function onSessions(sessions) {
	    console.log('', sessions);
	    IMData.sessions = nim.mergeSessions(IMData.sessions, sessions);
	    updateSessionsUI();
	}
	function onUpdateSession(session) {
	    console.log('', session);
	    IMData.sessions = nim.mergeSessions(IMData.sessions, session);
	    updateSessionsUI();
	}
	function updateSessionsUI() {
	    // 
	}
	
	function onRoamingMsgs(obj) {
	    console.log('', obj);
	    pushMsg(obj.msgs);
	}
	function onOfflineMsgs(obj) {
	    console.log('', obj);
	    pushMsg(obj.msgs);
	}
	function onMsg(msg) {
	    console.log('', msg.scene, msg.type, msg);
	    pushMsg(msg);
	}
	function pushMsg(msgs) {
	    if (!Array.isArray(msgs)) { msgs = [msgs]; }
	    var sessionId = msgs[0].sessionId;
	    IMData.msgs = IMData.msgs || {};
	    IMData.msgs[sessionId] = nim.mergeMsgs(IMData.msgs[sessionId], msgs);
	}
	
	function onOfflineSysMsgs(sysMsgs) {
	    console.log('', sysMsgs);
	    pushSysMsgs(sysMsgs);
	}
	function onSysMsg(sysMsg) {
	    console.log('', sysMsg)
	    pushSysMsgs(sysMsg);
	}
	function onUpdateSysMsg(sysMsg) {
	    pushSysMsgs(sysMsg);
	}
	function pushSysMsgs(sysMsgs) {
	    IMData.sysMsgs = nim.mergeSysMsgs(data.sysMsgs, sysMsgs);
	    refreshSysMsgsUI();
	}
	function onSysMsgUnread(obj) {
	    console.log('', obj);
	    IMData.sysMsgUnread = obj;
	    refreshSysMsgsUI();
	}
	function onUpdateSysMsgUnread(obj) {
	    console.log('', obj);
	    IMData.sysMsgUnread = obj;
	    refreshSysMsgsUI();
	}
	function refreshSysMsgsUI() {
	    // 
	}
	function onOfflineCustomSysMsgs(sysMsgs) {
	    console.log('', sysMsgs);
	}
	function onCustomSysMsg(sysMsg) {
	    console.log('', sysMsg);
	}
	
	function onSyncDone() {
	    console.log('');
	}
	
	return NIM.getInstance({
	    // SDK
	    // debug: true
	    appKey: '338f742de66a8283293644c990b7bbf6',
	    account: 'phz',
	    token: 'ecfb3279a5c100106aff93fd083643e4',
	    onconnect: onConnect,
	    onerror: onError,
	    onwillreconnect: onWillReconnect,
	    ondisconnect: onDisconnect,
	    // 
	    onloginportschange: onLoginPortsChange,
	    // 
	    // onblacklist: onBlacklist,
	    // onsyncmarkinblacklist: onMarkInBlacklist,
	    // onmutelist: onMutelist,
	    // onsyncmarkinmutelist: onMarkInMutelist,
	    // 
	    onfriends: onFriends,
	    onsyncfriendaction: onSyncFriendAction,
	    // 
	    onmyinfo: onMyInfo,
	    onupdatemyinfo: onUpdateMyInfo,
	    onusers: onUsers,
	    onupdateuser: onUpdateUser,
	    // 
	    onSuperTeams: onSuperTeams,
	    onSyncCreateSuperTeam: onSyncCreateSuperTeam,
	    onDismissSuperTeam: onDismissSuperTeam, 
	    onUpdateSuperTeamMember: onUpdateSuperTeamMember,
	    onUpdateSuperTeam: onUpdateSuperTeam, // 
	    onAddSuperTeamMembers: onAddSuperTeamMembers, // 
	    onRemoveSuperTeamMembers: onRemoveSuperTeamMembers,
	    // 
	    onteams: onTeams,
	    onsynccreateteam: onCreateTeam,
	    onteammembers: onTeamMembers,
	    onsyncteammembersdone: onSyncTeamMembersDone,
	    onupdateteammember: onUpdateTeamMember,
	    // 
	    onsessions: onSessions,
	    onupdatesession: onUpdateSession,
	    // 
	    onroamingmsgs: onRoamingMsgs,
	    onofflinemsgs: onOfflineMsgs,
	    onmsg: onMsg,
	    // 
	    onofflinesysmsgs: onOfflineSysMsgs,
	    onsysmsg: onSysMsg,
	    onupdatesysmsg: onUpdateSysMsg,
	    onsysmsgunread: onSysMsgUnread,
	    onupdatesysmsgunread: onUpdateSysMsgUnread,
	    onofflinecustomsysmsgs: onOfflineCustomSysMsgs,
	    oncustomsysmsg: onCustomSysMsg,
	    // 
	    onsyncdone: onSyncDone
	});
}
export default initNIM